module.exports = ({ constants }) => () => {
    const script = document.createElement('script');

    if (!constants.analytics.google) return script;

    script.addEventListener('load', () => {
        window.dataLayer = window.dataLayer || [];
        function gtag() {
            dataLayer.push(arguments);
        }
        gtag('js', new Date());
        gtag('config', 'UA-34497639-2');

        gtag('event', 'test', {
            event_category: 'cat'
        });

        // TODO: Return `gtag` and inject into components.
        window.gtag = gtag;
    });

    script.src = 'https://www.googletagmanager.com/gtag/js?id=UA-34497639-2';

    return script;
};
