module.exports = ({ stores, services }) => () => {
    const {
        sortTagsByRoleThenName,
        sortTagsByName,
        sortInstancesByTagThenMode
    } = services.tags;
    const { keepSortedBy } = stores.options.getState();

    const map = {
        orderAdded: stores.tags.getItems,
        roleThenName: sortTagsByRoleThenName,
        name: sortTagsByName
    };

    const tags = map[keepSortedBy]();

    return sortInstancesByTagThenMode(tags);
};
