const _ = require('lodash');

const init = require('./src/module-initialisation/initialise');

const stripMeta = (obj) => {
    return Object.entries(obj).reduce((acc, [key, val]) => {
        const newVal = _.isPlainObject(val) ? stripMeta(val) : val;
        return key.startsWith('__') ? acc : { ...acc, [key]: newVal };
    }, {});
};

module.exports = { init, stripMeta };
