const mapValues = (obj, map) => Object.fromEntries(Object.entries(obj).map(([key, val]) => [key, map(val)]));
const initValues = (obj, deps) => mapValues(obj, val => (val instanceof Function ? val(deps) : init(val, deps)));

const init = (obj, deps = {}) => {
    const { __modulename, ...target } = obj;
    if (!__modulename) return obj;
    const product = {};
    const newDeps = { [__modulename]: product, ...deps };
    return Object.assign(product, initValues(target, newDeps));
};

module.exports = init;
