module.exports = ({ services }) => async (email, fallback) => {
    const [profile, imageBlob] = await Promise.all([
        services.gravatar.fetchProfile(email),
        services.images.fromGravatar(email, fallback)
    ]);

    const name = profile
        ? extractName.fromProfile(profile)
        : extractName.fromEmail(email);

    return { name, imageBlob };
};

const extractName = {
    fromProfile: profile => {
        return profile.name.givenName || profile.displayName;
    },
    fromEmail: email => {
        return email.split('@')[0];
    }
};
