const libs = require('./');
const constants = require('./constants');
const { init } = require('@mattriley/module-initialiser');

module.exports = ({ global }) => {
    const util = init(libs.util, { global });
    const stores = init(libs.stores, { util });
    const services = init(libs.services, { util, constants, stores });
    const components = init(libs.components, { util, constants, stores, services });

    services.system.initialise();

    return {
        util,
        constants,
        stores,
        services,
        components
    };
};
