module.exports = () => (callback, milliseconds) => {
    let timeoutId;

    return () => {
        if (timeoutId) clearTimeout(timeoutId);
        timeoutId = setTimeout(() => {
            callback();
            timeoutId = null;
        }, milliseconds);
    };
};

/*

- Delays the invocation of `callback` until no further requests are made for `milliseconds`.
- Serves as an optimisation to ignore invocations made in quick succession.

*/
