module.exports = ({ constants, util, stores, services }) => {
    const adjustInstanceCounts = () => {
        const { planInstanceCountAdjustment, insertInstance, removeInstance } = services.tags;

        const tags = stores.tags.getItems();
        const modeCounts = util.pick(stores.options.getState(), constants.options.modes);
        const plans = planInstanceCountAdjustment(tags, modeCounts);

        plans.forEach(plan => {
            plan = { insert: [], remove: [], ...plan };
            plan.insert.forEach(insertInstance);
            plan.remove.forEach(removeInstance);
        });
    };

    return () => {
        return util.debounce(adjustInstanceCounts, constants.debounce.adjustInstanceCounts);
    };
};

