module.exports = ({ util, stores, components }) => () => {
    const $modals = document.createElement('div');
    $modals.className = 'modals';

    const done = () => {
        stores.app.setState({ modal: null });
    };

    const { modals, toggles } = Object.entries(components.modals).reduce(
        (acc, [key, modal]) => {
            const $modal = modal(done);
            acc.modals.push($modal);

            const { toggle } = util.visibilityToggle($modal);
            acc.toggles.push({ key, toggle });

            return acc;
        },
        { modals: [], toggles: [] }
    );

    stores.app.onChange('modal', ({ modal }) => {
        toggles.forEach(({ key, toggle }) => toggle(modal === key));
    });

    stores.app.onChange('modal', ({ modal }) => {
        document.body.style.overflow = modal ? 'hidden' : 'scroll';
    });

    $modals.append(...modals);

    return $modals;
};
