module.exports = ({ stores }) => () => {
    const $style = document.createElement('style');

    stores.toggles.onChange('customiseNilRole', ({ customiseNilRole }) => {
        const display = customiseNilRole ? 'inline-block' : 'none';

        $style.textContent = `
            .role-customiser.nil-role {
                display: ${display};    
            }`;
    });

    return $style;
};
